}



.logo_terminal {
    height: 1.5rem;
    padding-left: 4rem;
    padding-bottom: .4rem;
}
.about_terminal {
    height: 1.5rem;
    padding-left: .5rem;
    padding-top: .3rem;
    font: 500 .8em "Roboto Mono", monospace;
    color: #a6a6a6;
}
::-webkit-scrollbar {
