}


/* sidebar */
.mac_os{
    background-color: rgb(32, 30, 30);
    border-radius: 5px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    padding: 5px;
    margin-left:-1px;
    margin-right:-1px;


}
.mac_os_sub{
    display: -webkit-flex;
    justify-content: space-between;
    -webkit-justify-content: space-between;
    width:22%;
}
.mac_os_sub2{
    display: -webkit-flex;
    justify-content: space-between;
    -webkit-justify-content: space-between;
    width:6.5%;
}
.item {
    width: 12px;
    height: 12px;
    border-radius: .8rem;
    padding-right:10px;
}
.item-a {
    background: #ff3d3d;
}
.item-b {
    background: #ffb951;
}
.item-c {
    background: #3dff3d;
}
.sidebar {
    background-color: rgb(7, 7, 7);
    height: 100%;
    border-radius: 5px;
    padding: 0px 0px;
    box-shadow: 1px 15px 31px 15px rgba(0,0,0,0.75);
    text-align: center;
    border-color:rgb(36, 36, 36);
    border-width: 1px;
    border-style: solid;
    
}
.sidebar__icon:hover {
    transform: translateY(-0.7rem) scale(1.03);
    transition: transform .3s;

}

.sidebar__social-icons{
    padding-top: 15px;
}
.sidebar__avatar {
    margin-top: 20px;
    height: 110px;
    width: 120px;
    object-fit: contain;
    border-radius: 50%;
}
.sidebar__icon_small{
    width: 30px;
    height: 30px;
    text-align: center;
    padding: 5px;
}
.sidebar__icon {
    width: 50px;
    height: 50px;
    text-align: center;
    padding: 5px;
    transition: transform .3s;
}

.sidebar__name {
    margin: 10px 0;
    font-size: 2rem;
    color: white
}

.sidebar__name>span {
    color: #f17a7a;
}

.sidebar__item {
    height: 40px;
    font-size: 1rem;
    border-radius: 30px;
}

.sidebar__title {
    color: white;
    border-radius: 30px;
}

.sidebar__contact {
    text-align: left;
    color: white;
    padding: 2px 0;
    margin: 25px -15px 0 35px;
}

.sidebar__email {
    background-image: linear-gradient(to bottom, #ff3d3d, #ff3d3d);
    border-radius: 5px;
    color: #fff;
    margin: 15px 25px 0px;
    cursor: pointer;
    transition: all 3s linear;
}

.sidebar__email:hover {
    transform: scale(1.2);
}

.sidebar__resume{
    cursor: pointer;

}
.sidebar__resume:hover {
}

@media(max-width:991px) {
    .app__main-content {
