/* navbar */

.navbar__items {
    display: flex;
}

.navbar__active {
    margin-left:25px;
    font-size: 1.7rem;
    font-weight: 500;
    color: #ffffff;
    border-bottom: 4px solid #f17a7a;
}

.navbar__item {
    margin-right: 20px;
    text-decoration: none;
    color: #ffffff;
    font-size: 1.3rem;
    cursor: pointer;
}

.navbar__item:hover {
    background: -webkit-linear-gradient(#f17a7a, #f17a7a);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent
}


/* skill-card */

.skill-card {
    background-color: #f17a7a;
    margin: 10px;
    padding: 10px;
    display: block;
    flex-direction: row;
    border-radius: 5px;
    color:white;
    margin-top:30px;
    
}

.skill-card__icon {
    width: 40px;
    height: 40px;
    margin-bottom: 8px;
}

.skill-card__title{

    border-bottom: 2px solid white;
}
.skill_card__content {
    margin: 0;
    font-size: 0.9rem;
}
.skill_card__content2 {
    margin: 0;
    font-size: 0.9rem;
}
.skill_card__content3 {
    margin: 0;
    font-size: 0.9rem;
}

/* resume */

.resume {
    padding-top: 20px;
}

.resume-card__heading,
.reume-language__heading {
    margin-left: 10px;
    font-size: 1.6rem;
    font-weight: 700;
    color: white;
}

.resume-card__body {
    margin-top: 10px;
    color: white;

}

.resume-card__title {
    font-weight: 700;
}

.resume-card__name {
    font-size: 16px;
}

.resume-card__details {
    font-size: 0.9rem;
    padding-top: 5px;
}


/* bar */

.bar {
    width: 90%;
    background-color: #f1f2f3;
    border-radius: 20px;
    margin-bottom: 12px;
}

.bar__icon {
    width: 15px;
    height: 15px;
}

.bar__wrapper {
    background-image: linear-gradient(90deg, #00F260, #0575E6);
    border-radius: 20px;
    padding: 4px 14px;
    color: #fff;
    font-size: 0.9rem
}


/* projects */

.projects {
    height: 52vh;
    padding: 38px;
    overflow-y: auto;
    padding-top:5px;

}

.projectCard__wrapper {
    margin: 0 8px;
    width: 100%;
    height: 100%;
    min-height: 160px;
    position: relative;

}

.projectCard__image {
    width: 95%;
    height: 80%;
    border-radius: 30px;
    border: 3px solid #f17a7a;
    transition: transform 0.3s linear;
}


.img__description {
    position: absolute;
    top: 30%;
    bottom: 0;
    left: 0;
    right: 0;
    color: #f17a7a;
    visibility: hidden;
    opacity: 0;
    vertical-align: middle;
  }
  .projectCard__wrapper:hover .img__description {
    visibility: visible;
    opacity: 1;
  }
  .projectCard__wrapper:hover .projectCard__image {
    transform: scale(1.05);
    opacity: 0.05;
  }
.projectCard__icon {
    width: 25px;
    height: 25px;
    margin-right: 5px;
}

.projectCard__title {
    margin-top: 12px;
    font: size 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.projects__navbar {
    display: flex;
    margin-bottom: 25px;
    color: white;
}

.projects__navbar>div {
    margin: 0 5px;
    font-size: 1rem;
    cursor: pointer;
}

.projects__navbar-active {
    color: #f17a7a;
}

@media (max-width: 600px) {
    .app__main-content {
